



































































































































import {Component, Vue} from 'vue-property-decorator';
import BuilderNavbar from "@/components/BuilderUI/navbar/BuilderNavbar.vue";
import ShortcutMenu from "@/components/BuilderUI/shortcutmenu/ShortcutMenu.vue";
import ShortcutContent from "@/components/BuilderUI/shortcutmenu/ShortcutContent.vue";
import {Template, TemplateState} from "@/template/Template";
import {OMS} from "@/OMS";
import {Category, Request} from "@/api/URI";
import {CRUD} from "@/api/CRUD";
import {Auth} from "@/api/auth/Auth";
import {StandardElement} from "@/template/StandardElement";
import PreviewBox from "@/components/BuilderUI/PreviewBox.vue";
import PreviewElementSmartRenderer from "@/components/BuilderUI/PreviewElementSmartRenderer.vue";
import ElementPropertyEditor from "@/components/BuilderUI/ElementPropertyEditor.vue";

@Component({
  name: "ManageTemplate",
  components: {ElementPropertyEditor, PreviewElementSmartRenderer, PreviewBox, ShortcutContent, ShortcutMenu, Navbar: BuilderNavbar},
})

export default class ManageTemplate extends Vue {

  get activeTemplate(): Template { return OMS.getTemplate() }

  elements: Array<any> = [] as Array<any> ;
  selected: StandardElement = new StandardElement(0, "", "", "", "", {}, '', [], []);

  isShow = false;

  config = {
    icon: "flag",
    title: "องค์ประกอบพื้นฐาน",
    category: Category.STANDARD_ELEMENT,
  };

  isCalling = false;
  isStructureEditModalActive = false;
  isAddModalActive = false;

  newData: StandardElement = new StandardElement(0, '', '', '', '', {}, '', [], []);

  beforeMount() {
    this.getAll();
  }

  async getAll() {
    this.isCalling = true;

    const crud = new CRUD(this.config.category);
    crud.setRequest(Request.GET);
    await crud.shoot({});
    this.elements = crud.getResult();
    this.isCalling = crud.status;

    crud.log();
  }

  async onCreate() {
    this.isCalling = true;

    const crud = new CRUD(this.config.category);
    crud.setRequest(Request.CREATE);
    crud.setAttachData(this.newData);
    await crud.shoot({});
    this.isCalling = crud.status;

    crud.log();

    await this.getAll();
    this.isAddModalActive = false;
  }

  async onEdit() {
    this.isCalling = true;

    const crud = new CRUD(this.config.category);
    crud.setRequest(Request.UPDATE);
    crud.setAttachData(this.selected);
    await crud.shoot({});
    this.isCalling = crud.status;

    await this.getAll();
  }

  async onDelete() {
    this.$buefy.dialog.confirm({
      title: `ยืนยันการลบแม่แบบ ${this.selected.name}`,
      message: `<h4 class="title is-4">ลบแม่แบบ ${this.selected?.name}</h4>โดยการกระทำนี้นักออกแบบที่เป็นเจ้าของแม่แบบ รวมถึงนักการตลาดจะไม่สามารถใช้ข้อมูลจากแม่แบบนี้ได้ <br/><br/>สามารถกู้คืนแม่แบบนี้ได้ที่แท็บ <b>'แม่แบบที่ลบ'</b>`,
      cancelText: 'กลับ',
      confirmText: `ยืนยันลบแม่แบบ ${this.selected?.name}`,
      type: 'is-danger',
      hasIcon: true,
      icon: 'account-cancel',
      iconPack: 'mdi',
      onConfirm: async () => {
        this.isCalling = true;
        const crud = new CRUD(this.config.category);
        crud.setRequest(Request.DELETE);
        crud.setAttachData(this.selected);
        await crud.shoot({});
        this.isCalling = crud.status;

        // @ts-ignore
        this.selected = null;
        await this.getAll();
      }
    })
  }

  onClearSelected() {
    // @ts-ignore
    this.selected = null;
  }

}

