






































































import {Component, Prop, Vue} from 'vue-property-decorator';
import {TemplateElement} from "@/template/TemplateElement";

@Component({
  name: "ElementPropertyEditor",
  components: {},
})
export default class ElementPropertyEditor extends Vue {
  @Prop() element!: TemplateElement;
  @Prop() active = false;

  addProp(newValue: object) {
    this.element.props = {
      ...this.element.props,
      ...newValue
    };
  }

  removeProp(index: number) {
    // this.element.props.splice(index, 1);
  }

  save() {
    this.$emit('input', this.element);
  }
}
