

















import {Component, Prop, Vue, Watch} from "vue-property-decorator";
import Draggable from "vuedraggable";
import {TemplateElement} from "@/template/TemplateElement";
import {OMS} from "@/OMS";
import {AreaTag} from "@/template/area/AreaTag";

@Component({
  name: "PreviewElementSmartRenderer",
  components: {
    Draggable
  },
})

export default class PreviewElementSmartRenderer extends Vue {
  @Prop() element!: TemplateElement;
  showMenu = false;

  get isDebugMode(): boolean {
    return OMS.isDebugMode()
  }

  get dummyElement() {
    return this.element;
  }

  getComponentData(prop: { class: string, style: string, href: string }) {
    prop = {
      ...prop,
      class: `${prop.class} render-element`,
    }
    return {on: {}, attrs: prop};
  }

  created() {
    console.log(`%c[Child] Rendered: ${this.element.childTags || 'error'}`, 'color: #bada55');
  }

  @Watch("element")
  onSave() {
    console.log(`${this.element.getSign} has a change...!`);
  }

  hoverLeave(): void {
    this.showMenu = false;
  }

  onDrag(): void {
    console.log("onDrag");
    OMS.setHoveredElement(this.element);
  }

  onChoose() {
    OMS.setHoveredElement(this.element);
    console.log("onChoose")
    this.showMenu = true;
  }

  onEdit() {
    console.log(JSON.stringify(this.element));
    OMS.setSelectedElement(this.element);
  }

  isLastChild() {
    return this.element.children.length == 0;
  }

}
